.searchResult {
  .nvaBar {
    background-color: #d0584d;

    .title {
      color: #fff;
    }

    /deep/ .van-icon {
      color: #fff;
    }
  }
  .content {
    .content-item {
      padding: 0 15px;
      margin: 10px 0;
      border-bottom: 1px solid #f6f7f7;
    }
    padding: 0 10px;
  }
}
</style>
